<template>
  <v-container>
    <v-row class="pt-12">
      <v-col class="text-center">
        <div
          v-motion
          :initial="{
            opacity: 0.9,
            y: 10,
          }"
          :enter="{
            opacity: 1,
            y: 0,
            transition: {
              delay: 500,
              repeat: Infinity,
              duration: 1500,
              repeatType: 'mirror',
            },
          }"
          v-for="(circle, index) in circles"
          :key="index"
          class="circle"
          :style="circle.style"
        ></div>

        <v-card
          elevation="12"
          style="margin: auto"
          max-width="350"
          rounded="xl"
          class="pa-6"
          v-motion
          :initial="{ opacity: 0, x: 100 }"
          :enter="{
            opacity: 1,
            x: 0,
            scale: 1,
            transition: {
              duration: 200,
            },
          }"
          :variants="{ custom: { scale: 2 } }"
          :delay="200"
        >
          <v-card-title> الدخول لحسابي </v-card-title>
          <v-alert class="my-4" color="error" v-if="errMsg">{{
            errMsg
          }}</v-alert>
          <v-text-field
            variant="outlined"
            prepend-inner-icon="mdi-email"
            placeholder="البريد الالكتروني "
            color="primary"
            clearable
            rounded="xl"
            v-model="email"
            type="text"
            label="البريد الالكتروني"
            :rules="[rules.required]"
            class="my-4"
          >
          </v-text-field>
          <v-text-field
            variant="outlined"
            prepend-inner-icon="mdi-lock"
            placeholder="كلمة المرور  "
            color="primary"
            clearable
            rounded="xl"
            v-model="password"
            label="كلمة المرور "
            :append-inner-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, rules.min]"
            :type="show ? 'text' : 'password'"
            @click:append-inner="show = !show"
            hint="يجب ان يتكون من 8 رموز"
          >
          </v-text-field>
          <v-btn :loading="loading" color="primary" block @click="register"
            >الدخول</v-btn
          >
          <p class="py-4">او</p>
          <v-btn
            color="primary"
            variant="outlined"
            @click="signInWithGoogle"
            block
            >التسجيل بحساب google</v-btn
          >
          <v-divider color="grey"></v-divider>
          <p class="text-right pt-6">
            ليس لديك حساب ؟
            <v-btn
              class="px-0 mx-0"
              variant="text"
              to="register"
              color="primary"
              >التسجيل</v-btn
            >
          </p>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script setup>
import { ref, onMounted } from "vue";
import { auth } from "../../firebase.js";
import {
  getAuth,
  GoogleAuthProvider,
  signInWithPopup,
  signInWithEmailAndPassword,
} from "firebase/auth";
import { useRouter } from "vue-router";
import { useCounterStore } from "@/store/index.js";

const store = useCounterStore();
const circles = ref([]);
const colors = ["#fff3f3", "#fff9f3", "#ffe9e3"];
const email = ref("");
const password = ref("");
const router = useRouter();
const rules = ref({
  required: (value) => !!value || "الحقل مطلوب",
  min: (v) => v.length >= 8 || "يجب ان يتكون من 8 رموز او اكثر",
});
const show = ref(false);
const errMsg = ref();
const loading = ref(false);

const register = () => {
  errMsg.value = null;
  loading.value = true;

  signInWithEmailAndPassword(auth, email.value, password.value)
    .then((result) => {
      store.userData = result.user;
      localStorage.setItem("userData", JSON.stringify(result.user));
      router.push("/dashboard/allTravelsPage");
    })
    .catch((error) => {
      switch (error.code) {
        case "auth/invalid-email":
          errMsg.value = " البريد او كلمة المرور غير صحيحة  ";
          break;
        case "auth/user-not-found":
          errMsg.value = "المستخدم غير موجود";
          break;
        case "auth/wrong-password":
          errMsg.value = " البريد او كلمة المرور غير صحيحة";
          break;
        default:
          errMsg.value = "البريد او كلمة المرور غير صحيحة";
          break;
      }
    })
    .finally(() => {
      loading.value = false;
    });
};
const signInWithGoogle = () => {
  const provider = new GoogleAuthProvider();
  signInWithPopup(getAuth(), provider)
    .then((result) => {
      store.userData = result.user;
      localStorage.setItem("userData", JSON.stringify(result.user));

      router.push("/dashboard/allTravelsPage");
    })
    .catch((error) => {
      // handle error
    });
};
const generateRandomCircles = () => {
  const randomSize = getRandomSize();
  const newCircles = Array.from({ length: 10 }, () => ({
    style: {
      top: Math.floor(Math.random() * window.innerHeight) + "px",
      left: Math.floor(Math.random() * window.innerWidth) + "px",
      backgroundColor: getRandomColor(),
      width: randomSize + "px",
      height: randomSize + "px",
    },
  }));
  circles.value = newCircles;
};

const getRandomColor = () => {
  return colors[Math.floor(Math.random() * colors.length)];
};

const getRandomSize = () => {
  return Math.floor(Math.random() * 50) + 20; // Adjust size range as needed
};

onMounted(() => {
  generateRandomCircles();
});
</script>
<style>
.circle {
  position: absolute;
  border-radius: 50%;
}
</style>
